import { Project } from '../interfaces'
import projects from './projects.json'
import 'isomorphic-unfetch'


/** Static project data. */
export const dataArray: Array<Project> = projects;
/**
 * Calls a mock API which finds a project by SLUG from the list above.
 *
 * Throws an error if not found.
 */
export function findData(slug: string) {
    const selected = dataArray.find(data => data.slug === slug)

    if (!selected) {
        throw new Error('Cannot find project')
    }

    return selected
}

export async function getMarkdown(slug: string): Promise<string> {

    return new Promise<string>((resolve, reject) => {
        const selected = dataArray.find(data => data.slug === slug)
        if (!selected) {
            reject(new Error("No project matches the slug"));
        } else {
            const response = fetch(selected.markdownUrl,{headers:{'Content-Type': 'text/plain; charset=utf-8'}});
            response.then(
                value => { 
                    console.log("response.then");
                    return value.text() }
            ).catch(
                err => reject(new Error(err))
            ).then(
                value => {
                    if(value)
                    resolve(value);
                }
            ).catch();
        }

    })





    /*     const selected = dataArray.find(data => data.slug === slug)
        if(!selected){
            return Promise.reject(new Error("No project matches the slug")); 
        }
        const response = await fetch(selected.markdownUrl);
        if(!response.ok){
            return Promise.reject(new Error(response.statusText)); 
        }    
        console.log(response.text)
        return new Promise(response.text); */
}

/** Calls a mock API which returns the above array to simulate "get all". */
export function findAll() {
    // Throw an error, just for example.
    if (!Array.isArray(dataArray)) {
        throw new Error('Cannot find projects')
    }

    return dataArray
}
